import React, { Fragment, Component } from 'react';
import MainMenu from './Components/mainMenu/mainMenu';
import { Switch, Route, withRouter } from 'react-router-dom';
import { TransitionGroup, CSSTransition } from "react-transition-group";
import MainPage from './Components/mainPage/mainPage';
import PageContainer from './Components/pageContainer/pageContainer';
import AboutInfo from './Components/aboutInfo/aboutInfo';
import WorksPage from './Components/worksPage/worksPage';
import ContactsPage from './Components/contactsPage/contactsPage';
import Footer from './Components/footer/footer';
import menuList from './Components/mainMenu/menuList';

import './App.css';
import './transition.css';

class App extends Component {
  componentDidMount() {
    window.addEventListener("wheel", this.onScroll);
  }

  componentWillUnmount() {
    window.removeEventListener("wheel", this.onScroll);
  }

  onScroll = event => {
    let pathname = this.props.history.location.pathname;
    let deltaY = event.deltaY;

    const scrollHeight = Math.max(
      document.body.scrollHeight, document.documentElement.scrollHeight,
      document.body.offsetHeight, document.documentElement.offsetHeight,
      document.body.clientHeight, document.documentElement.clientHeight
    );
    
    if (scrollHeight !== document.documentElement.clientHeight + window.pageYOffset && deltaY > 0) return;
    if ( window.pageYOffset > 0 && deltaY < 0) return;
    if (this.wheeling || this.functionCall) return;

    let ticking;
    if (!ticking) {
      window.requestAnimationFrame(() => {
        const nextLocation = this.getNextLocation(pathname, menuList, deltaY);
        if (nextLocation !== pathname && nextLocation !== undefined) {
          this.changeLocation(nextLocation);
        }
        ticking = false;
      });

      ticking = true;
    }

    this.wheeling = true;
    this.functionCall = true;

    setTimeout(() => {
      this.wheeling = false;
      this.functionCall = false;
    }, 1500);
  };

  changeLocation(nextLocation) {
    this.props.history.push(nextLocation);
  }

  getNextLocation = (location, locationsArray, deltaY) => {
    let index = locationsArray.findIndex(el => el.link === location);
    if (index !== undefined && deltaY > 0 && index !== locationsArray.length - 1) {
      return locationsArray[index + 1].link;
    }
    if (index !== undefined && deltaY < 0 && index !== 0) {
      return locationsArray[index - 1].link;
    }
  }

  render() {
    const location = this.props.history.location;
    return (
      <Fragment>
        <MainMenu />
        <TransitionGroup component={null}>
          <CSSTransition key={location.pathname} exit={false} classNames="transition" timeout={1000}>
            <Switch location={location}>
              <Route path='/' exact render={() => (
                <PageContainer pageTitle="Nikolay Stepin">
                  <MainPage />
                </PageContainer>
              )} />
              <Route path='/about' render={() => (
                <PageContainer pageTitle="About me">
                  <AboutInfo />
                </PageContainer>
              )} />
              <Route path='/works' render={() => (
                <PageContainer pageTitle="Works">
                  <WorksPage />
                </PageContainer>
              )} />
              <Route path='/contacts' render={() => (
                <div className="wrapper">
                  <PageContainer pageTitle="Contacts" lastPage={true}>
                    <ContactsPage />
                  </PageContainer>
                  <Footer />
                </div>
              )} />
            </Switch>
          </CSSTransition>
        </TransitionGroup>
      </Fragment>
    )
  }
}

export default withRouter(App);